<template>
  <div>
    <div class="panel">
      <div class="panel-hd h36">
        <div class="isFixed">
          <h1></h1>
          <div class="panel-btn" v-if="cartGoodsNumber !== 0" @click="editorGoods($event)"></div>
        </div>
      </div>
      <div class="panel-bd">
        <div class="notice" v-if="totalPrice >= 88"><span></span></div>
        <ul class="service-policy" v-else>
          <li v-for="service in servicePolicy" :key="service"><i class="iconfont icon-quan"></i>{{service}}</li>
        </ul>
        <section v-if="cartGoodsNumber !== 0">
          <ul class="flex-list goods-list">
            <li v-for="(goods, index) in this.cartGoods" :key="index">
              <div class="key"><span class="checkbox" :checked="isShowEditor?editCheckedGoods[index]:goods.checked" @click="toggleChecked(goods,index)"><i class="iconfont icon-check"></i></span></div>
              <div class="info nowrap">
                <router-link :to="{path:'/goodsDetail',query:{id:goods.supId}}"><img :src="goods.pic" alt=""></router-link>
                <div class="txt">
                  <h4>{{goods.name}}</h4>
                  <p class="des">{{goods.sku}}</p>
                  <div class="txtbtm">
                    <div class="count fr">
                      <span @click="countGoodsNumber(goods,false)" :class="{disabled: goods.num === 1}">-</span>
                      <input type="number" v-model="goods.num" />
                      <span @click="countGoodsNumber(goods,true)">+</span>
                    </div>
                    <span>{{goods.price | priceFomat(goods.price)}}</span>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </section>
        <div class="nodata" v-else>
          <i class="icon icon-cart"></i>
          <p></p>
          <div v-if="typeof token !== 'string'"><router-link to="/login" class="btn"></router-link></div>
        </div>
      </div>
      <div class="panel-ft" v-if="cartGoodsNumber !== 0">
        <div class="info">
          <span class="checkbox" :checked="isSelectedAll" @click="selectedAll(isSelectedAll)"><i class="iconfont icon-check"></i></span>
          <span>({{selectedNum}})</span>
          <span class="fr price" v-if="!isShowEditor">{{totalPrice | priceFomat(totalPrice)}}</span>
        </div>
        <div class="key" v-if="!isShowEditor">
          <span :class="[{'disabled': selectedNum <= 0},'btn btn-full']"></span>
        </div>
        <div class="key" v-else>
          <span :class="[{'disabled': selectedNum <= 0},'btn btn-full']" @click="deleteGoods()"></span>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'
import Footer from '@/components/footer.vue'
import { Toast } from 'mint-ui'

export default {
  components: {
    Footer
  },
  data () {
    return {
      servicePolicy: ['30', '48', '88'],
      totalPrice: 0, // 
      isSelectedAll: true, // 
      selectedNum: this.$store.getters.cartGoodsNumber, // 
      isShowEditor: false, // 
      editCheckedGoods: {} // 
    }
  },
  // 
  filters: {
    // 
    priceFomat (price) {
      return '' + price.toFixed(2)
    }
  },
  created () {
    this.getAllGoodsPrice()
  },
  computed: {
    ...mapState(['cartGoods', 'token', 'username']),
    ...mapGetters(['cartGoodsNumber'])
  },
  methods: {
    // 
    countGoodsNumber (goods, flag) {
      if (flag) {
        goods.num++
      } else {
        if (goods.num <= 1) {
          Toast('1')
          goods.num = 1
          return
        }
        goods.num--
      }
      this.$store.dispatch('updateCart', this.cartGoods)
      this.getSelectedNumber()
      this.getAllGoodsPrice()
    },
    // 
    toggleChecked (goods, index) {
      if (!this.isShowEditor) {
        if (typeof goods.checked === 'undefined') {
          this.$set(goods, 'checked', true)
        } else {
          goods.checked = !goods.checked
        }
      } else {
        this.editCheckedGoods[index] = !this.editCheckedGoods[index]
        this.editCheckedGoods = {...this.editCheckedGoods}
      }
      this.hasSelectedAll()
      this.getSelectedNumber()
      this.getAllGoodsPrice()
    },
    // 
    getSelectedNumber () {
      let selectedNum = 0
      if (!this.isShowEditor) {
        Object.values(this.cartGoods).forEach((value, index) => {
          if (value.checked) {
            selectedNum += value.num
          }
        })
      } else {
        Object.values(this.editCheckedGoods).forEach((value, index) => {
          if (value) {
            selectedNum += 1
          }
        })
      }
      this.selectedNum = selectedNum
    },
    // 
    getAllGoodsPrice () {
      let totalPrice = 0
      Object.values(this.cartGoods).forEach((value, index) => {
        if (value.checked) {
          totalPrice += value.price * value.num
        }
      })
      this.totalPrice = totalPrice
    },
    // 
    selectedAll (flag) {
      this.isSelectedAll = !flag
      if (!this.isShowEditor) {
        Object.values(this.cartGoods).forEach((value, index) => {
          if (typeof value.checked === 'undefined') {
            this.$set(value, 'checked', !flag)
          } else {
            value.checked = !flag
          }
        })
      } else {
        for (let i in this.editCheckedGoods) {
          this.editCheckedGoods[i] = !flag
        }
      }
      this.getSelectedNumber()
      this.getAllGoodsPrice()
    },
    // 
    hasSelectedAll () {
      let flag = true
      if (!this.isShowEditor) {
        Object.values(this.cartGoods).forEach((value, index) => {
          if (!value.checked) {
            flag = false
          }
        })
      } else {
        if (Object.values(this.editCheckedGoods).length === Object.values(this.cartGoods).length) {
          Object.values(this.editCheckedGoods).forEach((value, index) => {
            flag = flag && value
          })
        } else {
          flag = false
        }
      }
      this.isSelectedAll = flag
    },
    // 
    editorGoods ($event) {
      this.isShowEditor = !this.isShowEditor
      if (this.isShowEditor) {
        event.target.innerText = ''
        for (let i in this.cartGoods) {
          this.editCheckedGoods[i] = false
        }
      } else {
        event.target.innerText = ''
      }
      this.hasSelectedAll()
      this.getSelectedNumber()
    },
    // 
    deleteGoods () {
      for (let i in this.editCheckedGoods) {
        if (this.editCheckedGoods[i]) {
          delete this.editCheckedGoods[i]
          delete this.cartGoods[i]
        }
      }
      this.editCheckedGoods = {...this.editCheckedGoods}
      this.$store.dispatch('updateCart', this.cartGoods)
      this.hasSelectedAll()
      this.getSelectedNumber()
      this.getAllGoodsPrice()
    }
  }
}
</script>

<style lang="stylus" scoped>
  .h36 {
    height 1.8rem
  }
  .panel {
    .panel-hd {
      background-color #fff
      .panel-btn {
        position absolute
        right .5rem
        top 0
      }
    }
    .panel-bd {
      padding-bottom 2rem
    }
    .panel-ft {
      bottom 2rem
      height 2rem
      line-height 2rem
      background-color #fff
      font-size .6rem
      .info {
        flex-grow 1
        padding 0 .5rem
        font-size 0
        span {
          font-size .6rem
        }
      }
      .btn {
        width 4.8rem
        &.disabled {
          background-color #ccc
          border-color #ccc
          pointer-events none
        }
      }
    }
  }
  .checkbox {
    display inline-block
    width .7rem
    height .7rem
    border 1px solid #d9d9d9
    background-color #fff
    border-radius 50%
    text-align center
    line-height .7rem
    margin-right .5rem
    .iconfont {
      font-size .6rem
      color #fff
    }
    &[checked] {
      background-color #b4282d
      border-color #b4282d
    }
  }
  .service-policy {
    background-color transparent
    color #666
    .iconfont {
      color #666
    }
  }
  .notice {
    display flex
    align-items center
    font-size .65rem
    color #f48f18
    background-color #fff8d8
    padding 0 .5rem
    height 1.7rem
    span {
      flex-grow 1
    }
  }
  .goods-list {
    & > li {
      padding 0
      border none
      &:not(:first-child) .info {
        border-top 1px solid #ececec
      }
      .key {
       padding .5rem 0
       align-self center
      }
      .info {
        padding .5rem .5rem .5rem 0
        img {
          width 3.4rem
          height 3.4rem
          background-color #f4f4f4
          margin-right .5rem
          float left
        }
        .txt {
          line-height 1
          .des {
            color #666666
            margin-top .25rem
          }
          .txtbtm {
            margin-top 1rem
            line-height 1rem
          }
        }
      }
    }
  }
  .count {
    height 1rem
    line-height 1rem
    input {
      line-height 1rem
      width 2rem
    }
    span {
      width 1.5rem
      font-size 1rem
    }
  }
</style>



