<template>
  <div>
    <topbar></topbar>
    <main class="login-wrap login-box">
      <h2 class="login-title"></h2>
      <div class="login-con">
        <form ref="registerForm">
          <ul class="flex-list">
            <li>
              <div class="info">
                <input type="number" maxlength="11" placeholder="" v-model="register.user" @keyup="showClear($event)" @blur="validateNumber()">
              </div>
              <div class="key" @click="clear()"><i class="iconfont icon-guanbi" v-show="isShowClear"></i></div>
            </li>
            <li>
              <div class="info">
                <input :type="inputType" placeholder="" v-model="register.password" @blur="validatePsd()">
              </div>
              <div class="key" @click="isShowPassword = !isShowPassword"><i :class="['iconfont',{'icon-bukejian': isShowPassword,'icon-kejian': !isShowPassword}]"></i></div>
            </li>
          </ul>
          <p class="error" v-show="isShowError">{{errorTxt}}</p>
          <div class="btn btn-full" @click="submitForm(register)"></div>
        </form>
      </div>
    </main>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import transferData from '@/tools/transferData.js'
import topbar from '@/components/topbar.vue'
import '@/assets/styles/login.styl'
export default {
  components: {
    topbar
  },
  data () {
    return {
      isShowClear: false, // 
      isShowPassword: false, // 
      errorTxt: '', // 
      isShowError: false, // 
      register: {
        // 
        user: '', // 
        password: '' // 
      }
    }
  },
  computed: {
    // 
    inputType () {
      if (this.isShowPassword) {
        return 'text'
      } else {
        return 'password'
      }
    }
  },
  methods: {
    // input
    showClear (event) {
      if (event.key !== '') {
        this.isShowClear = true
      } else {
        this.isShowClear = false
      }
    },
    // input
    clear () {
      this.register.user = ''
      if (this.register.user !== '') {
        this.isShowClear = true
      } else {
        this.isShowClear = false
      }
    },
    // 
    validateNumber () {
      if (this.register.user === '') {
        this.isShowError = true
        this.errorTxt = ''
        return false
      } else {
        let flag = /^((13|15|18|14|17)+\d{9})$/.test(this.register.user)
        if (!flag) {
          this.isShowError = true
          this.errorTxt = ''
          return false
        } else {
          this.isShowError = false
          return true
        }
      }
    },
    // 
    validatePsd () {
      if (this.register.password === '') {
        this.isShowError = true
        this.errorTxt = ''
        return false
      } else {
        let flag = /^\w{6,18}$/.test(this.register.password)
        if (!flag) {
          this.isShowError = true
          this.errorTxt = '6'
          return false
        } else {
          this.isShowError = false
          return true
        }
      }
    },
    // 
    submitForm (formName) {
      if (this.validateNumber() && this.validatePsd()) {
        transferData.userRegister(JSON.stringify(formName)).then(({data}) => {
          if (data.success) {
            Toast('')
            this.$router.push('/login')
          } else {
            Toast('')
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.btn {
  margin-top 1rem
}
</style>



