import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)

const state = {
  recommendData: {}, // 
  otherData: {}, // 
  allOtherData: {}, // 
  otherDataName: null, // name
  tagsHot: ['', '285', 'T31', '19920', '', '', '', ''], // 
  tagsHistory: [], // 
  suggestionArr: ['', '', '', '', '', '', '', '', '', '', ''], // 
  searchResultData: [], // 
  goodsId: null, // id
  goodsDetailData: {}, // 
  cartGoods: {}, // 
  token: window.sessionStorage.getItem('token'), // sessionStore.getItem('token'),
  username: window.sessionStorage.getItem('username') // 
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



