import axios from 'axios'
import store from '@/store'
import router from '@/router'

// axios
var instance = axios.create({
  timeout: 5000, // 5
  headers: { 'Content-Type': 'application/json;charset=UTF-8' }
})

// request
instance.interceptors.request.use(
  config => {
    // tokenhttp headertoken
    if (store.state.token) {
      config.headers.Authorization = `token ${store.state.token}`
    }
    return config
  }
)
// response
instance.interceptors.response.use(
  response => {
    return response
  },
  error => { // 2XX
    if (error.response) {
      switch (error.response.status) {
        case 401:
          router.replace({ // 
            path: 'login',
            query: { redirect: router.currentRoute.fullPath } //  path
          })
      }
    }
    return Promise.reject(error.response)
  }
)
export default {
  // 
  userRegister (data) {
    return instance.post('/api/register', data)
  },
  // 
  userLogin (data) {
    return instance.post('/api/login', data)
  },
  // 
  getUser () {
    return instance.get('/api/user')
  },
  // 
  mergeShoppingCart (data) {
    return instance.post('/api/cart/merge', data)
  },
  // 
  updateShoppingCart (data) {
    return instance.post('/api/cart/update', data)
  },
  getShoppingCart (data) {
    return instance.post('/api/cart/get', data)
  }
}



