<template>
  <div>
    <header class="flex center-head">
      <div class="key">
        <img src="//yanxuan.nosdn.127.net/8945ae63d940cc42406c3f67019c5cb6.png" alt="">
      </div>
      <div class="info">
        <p class="username">{{this.$store.state.username}}</p>
        <p></p>
      </div>
    </header>
    <main>
      <ul class="flex-block-3">
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li>
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li class="no-bottom">
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
        <li class="no-bottom">
          <i class="iconfont icon-shiwu"></i>
          <p></p>
        </li>
      </ul>
      <div class="mt10 mb10">
        <div class="btn btn-full btn-white" @click="logout()"></div>
      </div>
    </main>
    <Footer></Footer>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import Footer from '@/components/footer.vue'

export default {
  components: {
    Footer
  },
  methods: {
    logout () {
      MessageBox.confirm('?').then(action => {
        this.$store.dispatch('UserLogout')
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  .center-head {
    height 5.8rem
    background-image url(../../assets/images/center-bg.png)
    background-size 100%
    background-position bottom center
    padding-left .75rem
    overflow hidden
    color #fff
    img {
      width 3rem
      height 3rem
      border-radius 50%
    }
    .info {
      text-align left
      margin-left .75rem
      .username {
        font-size .75rem
        margin-bottom .25rem
      }
    }
  }
  .btn {
    height 2rem
    line-height 2rem
  }
</style>



