import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

import home from '@/pages/home/home.vue'
import shop from '@/pages/shop/shop.vue'
import classify from '@/pages/classify/classify.vue'
import classifyDetail from '@/pages/classify/children/classifyDetail.vue'
import cart from '@/pages/cart/cart.vue'
import center from '@/pages/center/center.vue'
import search from '@/pages/search/search.vue'
import searchResult from '@/pages/search/children/searchResult.vue'
import recommend from '@/pages/home/children/recommend.vue'
import other from '@/pages/home/children/other.vue'
import goodsDetail from '@/pages/goodsDetail/goodsDetail.vue'
import login from '@/pages/login/login.vue'
import register from '@/pages/login/children/register.vue'
import forget from '@/pages/login/children/forget.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  // mode: 'history',
  routes: [
    {
      // 
      path: '/home',
      component: home,
      children: [
        {
          // 
          path: '',
          component: recommend
        },
        {
          // 
          path: 'list',
          component: other
        }
      ]
    },
    {
      // 
      path: '/shop',
      component: shop
    },
    {
      // 
      path: '/classify',
      component: classify
    },
    {
      // 
      path: '/classifyDetail',
      component: classifyDetail
    },
    {
      // 
      path: '/cart',
      component: cart
    },
    {
      // 
      path: '/center',
      component: center,
      meta: {
        requireAuth: true
      }
    },
    {
      // 
      path: '/search',
      component: search
    },
    {
      // 
      path: '/searchResult',
      component: searchResult
    },
    {
      // 
      path: '/goodsDetail',
      component: goodsDetail
    },
    {
      // 
      path: '/login',
      component: login
    },
    {
      // 
      path: '/register',
      component: register
    },
    {
      // 
      path: '/forget',
      component: forget
    }
  ]
})
router.push('/home')

router.beforeEach((to, from, next) => {
  // 
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (store.state.token) {
      // 
      next()
    } else {
      // 
      next({path: '/login'})
    }
  } else {
    next()
  }
})

export default router



