<template>
  <div>
    <topbar></topbar>
    <div v-show="!isShowPhoneLogin">
      <main>
        <div class="login-wrap">
          <div class="login-img"><i class="icon-login"></i></div>
          <div class="login-con">
            <div class="btn btn-full" @click="isShowPhoneLogin = true"><i class="iconfont icon-shouji"></i> </div>
            <div class="btn btn-hollow btn-full"><i class="iconfont icon-youxiang"></i> </div>
            <p><router-link to="/register"> <i class="iconfont icon-xiangyou"></i></router-link></p>
          </div>
        </div>
      </main>
      <footer class="flex quick-login">
        <div class="key"><i class="iconfont icon-weixin"></i> </div>
        <div class="key"><i class="iconfont icon-qq"></i> QQ</div>
        <div class="key"><i class="iconfont icon-weibo"></i> </div>
      </footer>
    </div>
    <!---->
    <div class="login-wrap login-box" v-show="isShowPhoneLogin">
      <div class="login-img"><i class="icon-login"></i></div>
      <div class="login-con">
        <form>
          <ul class="flex-list">
            <li>
              <div class="info">
                <input type="number" maxlength="11" placeholder="" v-model="register.user" @keyup="showClear($event)" @blur="validateNumber()">
              </div>
              <div class="key" @click="clear()"><i class="iconfont icon-guanbi" v-show="isShowClear"></i></div>
            </li>
            <li>
              <div class="info">
                <input :type="inputType" placeholder="" v-model="register.password" @blur="validatePsd()">
              </div>
              <div class="key" @click="isShowPassword = !isShowPassword"><i :class="['iconfont',{'icon-bukejian': isShowPassword,'icon-kejian': !isShowPassword}]"></i></div>
            </li>
          </ul>
          <p class="error" v-show="isShowError">{{errorTxt}}</p>
          <div class="unlogin">
            <router-link to="/forget" class="fr"></router-link>
            <router-link to="/register"></router-link>
          </div>
          <div class="btn btn-full" @click="submitForm(register)"></div>
          <div class="btn btn-hollow btn-full" @click="isShowPhoneLogin = false"></div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
import {mapState} from 'vuex'
import transferData from '@/tools/transferData.js'
import topbar from '@/components/topbar.vue'
import '@/assets/styles/login.styl'
export default {
  components: {
    topbar
  },
  data () {
    return {
      isShowPhoneLogin: false, // 
      isShowClear: false, // 
      isShowPassword: false, // 
      errorTxt: '', // 
      isShowError: false, // 
      register: {
        // 
        user: '', // 
        password: '' // 
      }
    }
  },
  computed: {
    ...mapState(['cartGoods']),
    // 
    inputType () {
      if (this.isShowPassword) {
        return 'text'
      } else {
        return 'password'
      }
    }
  },
  methods: {
    // input
    showClear (event) {
      if (event.key !== '') {
        this.isShowClear = true
      } else {
        this.isShowClear = false
      }
    },
    // input
    clear () {
      this.register.user = ''
      if (this.register.user !== '') {
        this.isShowClear = true
      } else {
        this.isShowClear = false
      }
    },
    // 
    validateNumber () {
      if (this.register.user === '') {
        this.isShowError = true
        this.errorTxt = ''
        return false
      } else {
        let flag = /^((13|15|18|14|17)+\d{9})$/.test(this.register.user)
        if (!flag) {
          this.isShowError = true
          this.errorTxt = ''
          return false
        } else {
          this.isShowError = false
          return true
        }
      }
    },
    // 
    validatePsd () {
      if (this.register.password === '') {
        this.isShowError = true
        this.errorTxt = ''
        return false
      } else {
        let flag = /^\w{6,18}$/.test(this.register.password)
        if (!flag) {
          this.isShowError = true
          this.errorTxt = '6'
          return false
        } else {
          this.isShowError = false
          return true
        }
      }
    },
    // 
    submitForm (formName) {
      if (this.validateNumber() && this.validatePsd()) {
        transferData.userLogin(JSON.stringify(formName)).then(({data}) => {
          if (data.info === false) {
            Toast('')
            return
          }
          if (data.success) {
            Toast('')
            let token = data.token
            let username = data.username
            this.$store.dispatch('UserLogin', token)
            this.$store.dispatch('UserName', username)
            this.$store.dispatch('mergeCart', this.cartGoods)
            // 
            this.$router.push('/home')
          } else {
            Toast('')
          }
        })
      }
    }
  }
}
</script>



